*,
*::before,
*::after {
  box-sizing: border-box;
}
ul {
  padding: 0 0 0 0;
  margin: 0 0 0 0;
}
img {
  object-fit: contain;
}
li {
  list-style: none;
}
@media (max-width: 799px) {
  .container {
    padding: 0 20px 0 20px;
  }
