.general {
  position: relative;
}
.visually__hidden {
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.button {
  cursor: pointer;
  background-color: transparent;
  border: none;
}
.menu__list {
  display: flex;
  justify-content: space-between;
  position: relative;
}
.menu__item {
  display: flex;
  align-items: center;
}
.menu {
  padding: 10px 24px 10px 24px;
}
@media (max-width: 799px) {
  .menu {
    padding: 5px 15px 10px;
  }
}
.menu__burger {
  cursor: pointer;
  background-color: transparent;
  border: none;
  display: flex;
  justify-content: center;
  padding: 5px;
  height: 25px;
  align-items: center;
}
.menu__burger:hover .menu__toggle::before,
.menu__burger:hover .menu__toggle::after {
  background-color: #569ddc;
}
.menu__burger:hover > .menu__toggle {
  background-color: #569ddc;
}
.menu__burger.menu__burger--open .menu__toggle {
  background-color: transparent;
}
.menu__toggle {
  position: relative;
  display: inline-block;
  width: 30px;
  height: 2px;
  vertical-align: bottom;
  background-color: #004685;
}
.menu__toggle::before,
.menu__toggle::after {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: 2px;
  background: #004685;
  transition: top 0.5s 0.5s, transform 0.5s;
  transform-origin: 50% 50%;
}
.menu__toggle::before {
  top: 8px;
}
.menu__toggle::after {
  top: -8px;
}
.menu__toggle:hover {
  background-color: #569ddc;
}
.menu__burger--open .menu__toggle {
  background: 0 0;
  transition: background 0.1s 0.1s;
}
.menu__burger--open .menu__toggle::after,
.menu__burger--open .menu__toggle::before {
  transition: top 0.3s, transform 0.3s 0.5s;
  width: 30px;
  top: 0;
}
.menu__burger--open .menu__toggle::before {
  transform: rotate3d(0, 0, 1, 45deg);
  background-color: #004685;
}
.menu__burger--open .menu__toggle::after {
  transform: rotate3d(0, 0, 1, -45deg);
  background-color: #004685;
}
.menu__button {
  cursor: pointer;
  background-color: transparent;
  border: none;
}
.menu__help {
  position: absolute;
  pointer-events: none;
  right: -100%;
  display: none;
}
.menu__help--open {
  min-width: 241px;
  pointer-events: all;
  display: block;
  box-shadow: 0px 3px 12px #00000033;
  background-color: #FFFFFF;
  padding: 25px 16px 25px 16px;
  top: 57px;
  right: 0;
}
.menu__help--open::after {
  content: "";
  top: -24px;
  position: absolute;
  right: 16px;
  border: 12px solid transparent;
  border-bottom: 12px solid #FFFFFF;
  z-index: 1;
}
.menu__item--relative {
  position: relative;
}
@media (max-width: 799px) {
  .menu__item--relative {
    display: none;
  }
}
.menu__img {
  display: flex;
  width: 44px;
  height: 44px;
  background-image: url('../img/ic_help.svg');
}
.menu__img:hover {
  display: flex;
  background-image: url('../img/ic__help--hover.svg');
  background-repeat: no-repeat;
  background-size: auto;
  width: 44px;
  height: 44px;
  z-index: 1;
}
@media (max-width: 799px) {
  .menu__list--reverse {
    flex-direction: row-reverse;
  }
}
@media (max-width: 799px) {
  .menu__svg {
    width: 195px;
    height: 30px;
  }
}
.search {
  position: relative;
}
.search__head {
  font-size: 36px;
  font-weight: 600;
  color: #FFFFFF;
  margin: 0 0 0 0;
}
@media (max-width: 799px) {
  .search__head {
    font-size: 20px;
    margin: 2px 0 5px 0;
  }
}
.search__item:first-child {
  margin-bottom: 17px;
}
.search__input {
  font-size: 16px;
  width: 100%;
  height: 100%;
  border: 2px solid #00468533;
  border-radius: 8px;
  font-weight: 400;
  padding: 19px 24px 19px 24px;
  outline: none;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.search__input:focus::placeholder {
  color: #00000033;
}
@media (max-width: 799px) {
  .search__input {
    padding: 13px 50px 9px 9px;
  }
}
.search__label {
  display: block;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.search__fieldset {
  padding: 0;
  border: none;
  width: 100%;
}
.search__form--flex {
  display: flex;
}
@media (max-width: 799px) {
