    width: 100%;
    padding: 40px 20px 30px 15px;
  }
}
@media (min-width: 1280px) {
  .search__list {
    margin: 0 auto;
  }
}
.search__button {
  cursor: pointer;
  background-color: transparent;
  border: none;
  padding: 14px 0 14px 0;
