.content__row {
  position: relative;
}
.content__row::after {
  content: '';
  position: absolute;
  width: 100%;
  display: block;
  border-top: 1px solid #00468533;
  left: 0;
}
.content__row::before {
  content: '';
  position: absolute;
  width: 100%;
  display: block;
  border-bottom: 1px solid #00468533;
  top: 0;
}
@media (max-width: 799px) {
  .content__row::before {
    content: none;
  }
}
.content__block--reverse {
  flex-direction: row-reverse;
}
.content__block--reverse .content__figure {
