.content__link--underline {
  text-decoration: underline;
}
@media (max-width: 799px) {
  .content__box {
    display: flex;
    align-items: center;
  }
}
@media (min-width: 800px) {
  .content__svg {
    display: none;
  }
}
.content__list--row {
  display: flex;
