.side-menu__heading {
  font-size: 16px;
  font-weight: 700;
  text-transform: uppercase;
  margin-top: 19px;
}
.side-menu {
  opacity: 0;
  top: 0;
  left: -100%;
  pointer-events: none;
  position: fixed;
  height: 100%;
  transition: opacity 0.5s ease, left 0.5s ease;
  z-index: 3;
}
@media (max-width: 799px) {
  .side-menu {
    right: -100%;
    transition: opacity 0.5s ease, right 0.5s ease;
    left: unset;
  }
}
.side-menu__open {
  pointer-events: all;
  opacity: 1;
  left: 0;
}
@media (max-width: 799px) {
  .side-menu__open {
    right: 0;
    left: unset;
  }
}
.side-menu__inner {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  box-shadow: 0px 3px 12px #00000029;
  opacity: 0.95;
  background-color: #FFFFFF;
  padding: 100px 20px 80px 20px;
  overflow: auto;
}
.side-menu__wrapper {
  height: 100%;
}
.side-menu__link {
  font-weight: 400;
  font-size: 16px;
  color: #004685;
  transition: 0.5s ease;
}
.side-menu__link:hover {
  color: #569ddc;
}
.side-menu__list {
  margin-bottom: 42px;
}
.side-menu__item--margin {
  margin-bottom: 19px;
}
.side-menu__item {
  padding: 9px 12px 11px 18px;
}
.side-menu__item--burger {
  position: absolute;
  right: 0;
  top: 0;
}
.help__text {
  font-size: 16px;
  font-weight: 400;
}
.help__link {
  font-size: 16px;
  color: #004685;
}
.help__link--phone {
  font-size: 28px;
  color: #000000;
  text-decoration: none;
  font-weight: 600;
}
.help__item--margin {
  margin-bottom: 21px;
  margin-top: 5px;
}
.help__list {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.modal__desc {
  color: #000000;
  font-weight: 400;
  font-size: 16px;
  max-width: 267px;
  margin: 9px 0 30px 0;
}
.modal__sign {
  font-size: 24px;
  font-weight: 600;
  color: #FFFFFF;
}
.modal__send {
  border-radius: 8px;
  background-color: #d30531;
  border: none;
  cursor: pointer;
  transition: background 0.5s ease;
  padding: 14px 32px 14px 32px;
  width: 100%;
}
.modal__send:hover {
  background-color: #E1133F;
}
.modal__input {
  border-radius: 8px;
  border: 2px solid #00000075;
  padding: 19px 24px 19px;
  width: 100%;
  transition: 0.5s ease;
}
.modal__input:focus {
  border-color: #00468533;
  outline: none;
}
.modal__input:focus::placeholder {
  transition: 0.5s ease;
  color: #00000033;
}
.modal__link {
  color: #004685;
  font-size: 16px;
  font-weight: 400;
  transition: 0.5s ease;
}
.modal__link:hover {
  color: #569ddc;
}
.modal__list {
  display: grid;
  gap: 16px;
}
.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.5);
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.5s, visibility 0.5s;
  z-index: 3;
}
.modal__overlay {
  background-color: #fff;
  padding: 40px;
  max-height: 470px;
  height: 100%;
  overflow-y: auto;
  opacity: 1;
  transition: opacity 0.5s, transform 0.5s;
  position: relative;
  width: 400px;
}
@media (max-width: 799px) {
  .modal__overlay {
    max-height: unset;
    width: 100%;
    height: 100%;
  }
}
.modal__open {
  opacity: 1;
  visibility: visible;
  overflow-y: scroll;
}
.modal__overlay.modal__open {
  opacity: 1;
}
.modal__list--flex {
  display: flex;
}
.modal__close {
  display: flex;
  border: none;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  cursor: pointer;
  background-color: transparent;
  width: 32px;
  height: 32px;
  position: relative;
}
.modal__close::before,
.modal__close::after {
  content: "";
  display: block;
  width: 30px;
  height: 2px;
  background-color: #00000040;
}
.modal__close::after {
  transform: rotate(45deg);
}
.modal__close::before {
  transform: rotate(-45deg);
  margin-bottom: -2px;
}
.modal__close:hover::before,
.modal__close:hover::after {
  background-color: #569ddc;
}
.modal__item--position {
  position: absolute;
  top: 10px;
  right: 8px;
}
.modal__list--authorization {
  margin-top: 53px;
}
@media (max-width: 799px) {
  .modal__list--authorization {
    margin-top: 32px;
  }
}
.body__overflow {
  overflow-y: hidden;
  scrollbar-gutter: stable;
}
.body__overflow::before {
  display: flex;
  content: '';
  background-color: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: 2;
}
.body {
  font-family: 'Open Sans', sans-serif;
  color: #000000;
}
.anchor__svg {
  width: 50px;
  height: 50px;
  fill: #00468533;
}
.anchor__up--button {
  display: flex;
  cursor: pointer;
  background-color: transparent;
  border: none;
  transform: rotate(270deg);
  position: fixed;
  bottom: 0;
  right: 0;
}
.anchor__up--button .anchor__svg:hover {
  transition: fill 0.5s ease;
  fill: #00000075;
}
.anchor__up--button-hide {
  display: none;
}
